const _ROMAN_TABLE = {
  1000: "M",
  900: "CM",
  500: "D",
  400: "CD",
  100: "C",
  90: "XC",
  50: "L",
  40: "XL",
  10: "X",
  9: "IX",
  5: "V",
  4: "IV",
  1: "I"
};

const ROMAN_TABLE = {
  1: "I",
  4: "IV",
  5: "V",
  9: "IX",
  10: "X",
  40: "XL",
  50: "L",
  90: "XC",
  100: "C",
  400: "CD",
  500: "D",
  900: "CM",
  1000: "M"
};

const ROMAN_TABLE_KEYS = Object.keys(ROMAN_TABLE);

export const fromNumber = (value) => {
  if (typeof value !== 'number' || value < 0 || !Number.isInteger(value)) {
    throw new Error('No number');
  }

  let result = '';
  ROMAN_TABLE_KEYS.forEach(key => {
    while (value >= key) {
      value -= key;
      result += ROMAN_TABLE[key];
    }
  });
  return result;
};
